import axios from 'axios'

const welcome = {
  namespaced: true,
  state: {
    _demo: 'dsdsd'
  },
  getters: {
    _getDemo (state) {
      return state._demo
    }
  },
  mutations: {
    _setDemo (state, _demo) {
      state._demo = _demo
    }
  },
  actions: {
    login (context, param) {
      let url = '/auth/login'
      return new Promise((resolve, reject) => {
        axios.post(url, param)
          .then(res => {
            resolve(res)
          })
          .catch(e => {
            reject(e)
          })
      })
    }
  }
}

export default welcome



