import Vue from 'vue'
import Router from 'vue-router'

// Import Component
import Welcome from '@/components/Welcome'
import Test1 from '@/components/Test1'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Welcome',
      component: Welcome
    },
    {
      path: '/Test1',
      name: 'Test1',
      component: Test1
    }
  ]
})



