!<template>
    <el-main>
        <el-row>
            <el-calendar v-model="value"></el-calendar>
        </el-row>
        <!-- <el-row style="margin-top: 10px">
            <el-backtop target=".page-component__scroll .el-scrollbar__wrap"></el-backtop>
        </el-row> -->
        <el-row>
            <el-button @click="onClick">Back</el-button>
        </el-row>
    </el-main>
</template>

<script>
export default {
  data () {
    return {
      value: new Date()
    }
  },
  mounted () {
    this.initPage()
  },
  methods: {
    initPage () {
    //   console.error(this.$route.params)
    //   this.$message('Params : ' + this.$route.params.id)
    },
    onClick () {
      this.$router.push('/')
    }
  }
}
</script>

<style>
@import '../assets/css/abc.css';
</style>



